* {
  margin: 0;
  padding: 0;
}
ul {
  list-style: none;
}

.user {
  margin-left: auto;
  width: fit-content;
  padding: 10px 20px;
  background-color: #ff3131;
  color: white;
  margin-top: 20px;
  border-radius: 10px;
  border-top-right-radius: 0px;
}
.oponent {
  display: flex;
  justify-content: start;
  margin-top: 20px;
  background-color: #7ed957;
  color: white;
  width: fit-content;
  padding: 10px 20px;
  border-radius: 10px;

  border-top-left-radius: 0px;
}

img {
  width: 100%;
  height: 100%;
  border-radius: 50px;
}
